#index-header {
    display: none;
  }

    #mobile-header {
    position: sticky;
    top: 0;
    z-index: 3;
    width: 100%;
    border-bottom: solid 4px var(--main-color);
    display: block;

}

#mobile-nav {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--third-color)
  );
  width: 100%;
}

#mobile-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  height: 70px;
  box-sizing: border-box;
}

#mobile-logo {
  height: 100%;
  display: flex;
  align-items: center;

}

#mobile-logo img {
  height: 100%;
  border: solid white 2px;
  transition: 0.1s;
  width: 130px;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

#mobile-menu {
  background-color: var(--third-color);
  height: 100vh;
  width: 99%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  margin-left: 0.5%;
  overflow: hidden;
display: none;
}

#mobile-menu-close{
  width: 100%;
  font-size: 30px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3%;
  box-sizing: border-box;
  cursor: pointer;
  height: 7vh;
  margin-bottom: 0;

}

#mobile-menu-links{

  margin-top: 0;
  list-style: none;
  width: 100%;
  height: 63%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  width: 100%;
  cursor: pointer;
  height: 12.5%;
}

.mobile-menu-links-li-a{
  text-decoration: none;
  color: var(--fourth-color);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#mobile-menu-links-selected{
  background-color: var(--fourth-color);
}
#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info{
  margin-top: 0;
  background-color: black;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.mobile-menu-info{
  width: 100%;
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
}

.mobile-menu-info-div1{
  height: 100%;
  min-width: 30%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 2%;
  box-sizing: border-box;
}

.mobile-menu-info-div1 h4 {
  color: white;
  margin-left: 4%;
}

.mobile-menu-info-anchor{

  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1%;
  overflow-x: scroll;
  text-decoration: none;
  color: white;

}

#mobile-menu-info-social{
  border: solid white 2px;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  margin-top: 0;
  display: flex ;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.mobile-menu-info-social-li{
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}


.mobile-menu-info-social-li2{
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-info-social-li2-img{
  height:  20px;
  width: 20px;
}

/* LOGIN MAIN */

#login-main{
  display: none;
}

#login-main_mobile{
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 1%;
  background-color: var(--third-color);
  min-height: 90vh;
  overflow-y: scroll;
}

#login_main_section_mobile{
  border: solid var(--fourth-color) 2px;
  border-radius: 8px;
  margin-top: 5%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

#login_main_section_h2_mobile{
 
  padding: 2%;
  background-color: rgba(192, 200, 250,0.8);
  color: var(--third-color);
  border-radius: 5px;
  margin-bottom: 3% ;
}

#login_main_section_ul_mobile{
 
  background-color: rgba(192, 200, 250,0.8);
  color: var(--third-color);
  border-radius: 5px;
  margin-bottom: 3%;
}

.login_main_section_ul_li_mobile{
 
  padding: 2%;

}

#board_members_form_mobile{
  margin-top: 3%;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2%;
  background-color: rgba(192, 200, 250,0.8);
  border-radius: 8px;
}

#board_members_form_mobile_fieldset{
  border: solid white 2px;
  width: 90%;
    display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: rgb(64, 68, 106);
  margin-top: 2%;
  border-radius: 5px;
  margin-bottom: 2%;
}

#board_members_form_mobile_fieldset_legend{
  border: solid white 2px;
  color: white;
  border-radius: 5px;
  padding: 1%;
  background-color: rgb(64, 68, 106) ;
}

.board_members_form_mobile_fieldset_div{

  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 1%;
  box-sizing: border-box;
  color: white;
}

.board_members_form_mobile_fieldset_label{

  margin-right: 1%;
  font-size: 16px;
  max-width: 30%;
}

.board_members_form_mobile_fieldset_label_input{

  min-width: 60%;
  max-width: 70%;
  background-color: inherit;
  outline: none;
  border: none;
  border-bottom: solid white 2px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  overflow-x: scroll;
}
.board_members_form_mobile_fieldset_label_input:focus{
  background-color: black;
}
.board_members_form_mobile_fieldset_label_input:hover{
  background-color: black;
}

#board_members_form_mobile_fieldset_div_submit_and_reset{

  display: flex;
  justify-content: center;
  align-items: center;
}

#board_members_form_mobile_password_submit{
  background-color: var(--third-color);
  border: solid white 1px;
  color: white;
  padding: 1.5%;
  cursor: pointer;
  margin-right: 2%;
   border-radius: 5px;
}
#board_members_form_mobile_password_submit:hover{
  border: solid white 1px;
  background-color: white;
  color: var(--third-color);
}

#board_members_form_mobile_password_reset{
  background-color: inherit;
  padding: 1.5%;
  color: white;
  border: solid rgb(64, 68, 106)  1px;
  cursor: pointer;
  border-radius: 5px;
}
#board_members_form_mobile_password_reset:hover{
  border: solid white 1px;
}

#board_members_form_mobile_div1{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2%;
  box-sizing: border-box;

}

#board_members_form_mobile_loading{
  display: none;
}

#board_members_form_mobile_error_msg{
  background-color: darkred;
  border-radius: 8px;
  padding: 1.5%;
  color: white;
  display: none;
}


#login_main_h3_mobile{
  background-color: var(--fourth-color);
  color: var(--third-color);
  padding: 2%;
  width: 80%;
  border-radius: 5px;
  text-align: center;
}




/*   FOOTER */
#index-footer{
  display: none;
}
#index-footer-mobile {
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 0px;
    padding: 2%;
}

#index-footer-mobile-div1 {
  text-align: center;

}

#index-footer-mobile-p1 {
  font-size: 13px;
  line-height: 1.4;
  margin: 0;
  
}


#index-footer-mobile-div2 {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2 {
  font-size: 13px;
  margin: 0;
}

#index-footer-mobile-a1 {
  font-size: 13px;
  color: white;
  text-decoration: underline;
  text-decoration-color: white;
  padding: 6px 10px;
  border-radius: 6px;
  transition: 0.3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* ADMIN */
#index-footer-mobile-div3 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2{
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  color: var(--fourth-color);
}
#index-footer-mobile-p2:hover{
  color: white;
}
